body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.loading-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.loading-image {
  width: 150px;
  height: auto;
}

.bounce {
  animation: bounce 1.5s infinite;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}

.content {
  width: 100%;
}

.header {
  display: flex;
  align-items: center;
  padding: 20px;
  background-color: #282c34;
  color: white;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
}

.logo {
  width: 50px;
  height: auto;
  margin-right: 20px;
  transition: all 0.5s ease;
}

.header-title {
  font-size: 1.5em;
}

.main-content {
  padding-top: 100px; /* To account for fixed header */
  padding: 20px;
}

.section {
  margin: 20px 0;
}
